* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }
  *:before, *:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }

*::-webkit-input-placeholder {
  color: #666;
  opacity: 1; }

*:-moz-placeholder {
  color: #666;
  opacity: 1; }

*::-moz-placeholder {
  color: #666;
  opacity: 1; }

*:-ms-input-placeholder {
  color: #666;
  opacity: 1; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  color: #666; }

body input:required:valid,
body textarea:required:valid {
  color: #666; }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
  list-style-type: none; }

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.65;
  font-family: "poppins", sans-serif;
  overflow-x: hidden;
  opacity: 1;
  margin-top: 84px; }

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto; }

.nav {
  transition: all .25s; }
  .nav ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center; }
  .nav li:last-of-type a {
    padding-right: 0; }
  .nav a {
    color: #ffffff;
    font-size: 21px;
    letter-spacing: .8px;
    display: inline-block;
    padding: 10px 20px;
    transition: all .25s; }
    .nav a:hover {
      color: #000000; }
  .nav .fa {
    font-size: 18px;
    margin-right: 15px; }

.logo {
  text-align: center;
  line-height: 1;
  transition: all .25s; }
  .logo__brand {
    font-size: 38px; }
  .logo__description {
    letter-spacing: 0.5px;
    font-size: 12px; }
  .logo a {
    display: inline-block;
    color: #fff;
    text-decoration: none;
    opacity: 1;
    transition: all .25s; }
    .logo a:hover {
      opacity: .95; }

.testfont p {
  background-color: black;
  display: flex;
  border-radius: 11em; }
  @media (max-width: 575px) {
    .testfont p {
      display: none; } }
  .testfont p::before {
    left: -50px;
    font-size: 50px; }
  .testfont p:nth-of-type(1) {
    font-weight: 300; }
    .testfont p:nth-of-type(1)::before {
      content: "";
      display: block;
      position: absolute;
      font-family: "Font Awesome 5 Free";
      line-height: 1;
      font-weight: 900; }
  .testfont p:nth-of-type(2) {
    font-weight: normal; }
    .testfont p:nth-of-type(2)::before {
      content: "";
      display: block;
      position: absolute;
      font-family: "Font Awesome 5 Brands";
      line-height: 1; }
  .testfont p:nth-of-type(3) {
    font-weight: 600; }
    .testfont p:nth-of-type(3)::before {
      content: "";
      display: block;
      position: absolute;
      font-family: "Material Icons";
      line-height: 1; }
