@import "fonts";
@import "libs";
@import "vars";
@import "_mixins/mixins";


* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	&:before, &:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box; } }

*::-webkit-input-placeholder {
	color: #666;
	opacity: 1; }

*:-moz-placeholder {
	color: #666;
	opacity: 1; }

*::-moz-placeholder {
	color: #666;
	opacity: 1; }

*:-ms-input-placeholder {
	color: #666;
	opacity: 1; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
	color: #666; }
body input:required:valid,
body textarea:required:valid {
	color: #666; }

a {
	text-decoration: none;
	&:hover {
		text-decoration: none; } }

ul, li {
	padding: 0;
	margin: 0;
	list-style: none;
	list-style-type: none; }


body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.65;
	font-family: $default-font;
	overflow-x: hidden;
	opacity: 1;
	margin-top: 84px; }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }


.nav {
	@include trs;
	ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		align-items: center; }
	li {
		&:last-of-type {
			a {
				padding-right: 0; } } }
	a {
		color: #ffffff;
		font-size: 21px;
		letter-spacing: .8px;
		display: inline-block;
		padding: 10px 20px;
		@include trs;
		&:hover {
			color: #000000; } }
	.fa {
		font-size: 18px;
		margin-right: 15px; } }


.logo {
	text-align: center;
	line-height: 1;
	@include trs;
	&__brand {
		font-size: 38px; }
	&__description {
		letter-spacing: 0.5px;
		font-size: 12px; }
	a {
		display: inline-block;
		color: $white;
		text-decoration: none;
		opacity: 1;
		@include trs;
		&:hover {
			opacity: .95; } } }


.testfont {
	p {
		background-color: black;
		display: flex;
		border-radius: 11em;
		@include media-breakpoint-down(xs) {
			display: none; }
		&::before {
			left: -50px;
			font-size: 50px; }
		&:nth-of-type(1) {
			font-weight: 300;
			&::before {
				@include before-fa("\f29d"); } }
		&:nth-of-type(2) {
			font-weight: normal;
			&::before {
				@include before-fab("\f368"); } }
		&:nth-of-type(3) {
			font-weight: 600;
			&::before {
				@include before-mi('\e84d'); } } } }
